let dots = document.querySelector('.dots');

let slider = new KeenSlider('#my-keen-slider',{
    loop:true,
    created:(slider)=>{
        slider.track.details.slides.forEach((e,ind)=>{
        let dot = createDiv('dot');
        dot.addEventListener("click", () => slider.moveToIdx(ind));
        dots.append(dot);
        })
        
        dots.children[slider.track.details.rel].classList.add('activeDot')
        
    },
    slideChanged:(slider)=>{
        dots.childNodes.forEach((e,ind)=>{
            e.classList.remove('activeDot');
            if(ind == slider.track.details.rel){
                e.classList.add('activeDot');
            };
        })
    }
});




document.querySelector('.reviews__slider').addEventListener('click',(e)=>{
if(e.target.closest('.slider__arrow')){
    switch(e.target.closest('.slider__arrow').dataset.arrow){
        case 'left':
            slider.prev();
        break;
        case 'right':
            slider.next();
        break;
    }
}
});

function createDiv(className) {
    var div = document.createElement("div");
    var classNames = className.split(" ");
    classNames.forEach((name) => div.classList.add(name));
    return div;
}



let modalsOverlay = document.querySelector('.modals__overlay');
let modal = document.querySelector('.modal');
let closeModal = document.querySelector('.close__modal');



closeModal.addEventListener('pointerup',closePopap);

function closePopap(e){
    modalsOverlay.classList.remove('modals__overlay--visible');
    modal.classList.remove('modal--visible');
}

document.querySelectorAll('.popap').forEach((e)=>{

    e.addEventListener('pointerup',(e) => {
        modalsOverlay.classList.add('modals__overlay--visible');
        modal.classList.add('modal--visible');
    })

})




document.querySelector('.main__burger').addEventListener('pointerup',function(e){
    document.querySelector('.nav__inner').classList.toggle('hide');
    document.querySelector('.main__burger').classList.toggle('swap');
})